    async nextStep() {
        let next_step = this.state.step + 1;

        if (next_step === 12) {
            await bridge.send("VKWebAppJoinGroup", {"group_id": id_vk_group}).then(() => {
            }).catch(() => {
            });
        }

        this.setState({
            answer: 0,
            step: next_step,
            next_button_active: 0
        })
    }

    restart() {
        if (this.state.share_in_story === 1) {
            this.resetState();
        } else {
            this.props.ShowStoryModal();
        }
    }

    resetState() {
        this.setState({
            gender: 0,
            answer: 0,
            step: 0,
            next_button_active: 0,
            share_in_story: 0,
        })
    }

    async componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps !== this.props) {
            if (this.props.shareInStory !== null) {
                if (this.props.shareInStory === 1) {
                    await this.share_result(1);
                } else {
                    this.resetState();
                }

                this.props.setShareInStory(null);
            }
        }

        if (prevState !== this.state) {
            let {step, selected_charaster} = this.state;
            let {result_charasters} = this;

            if (step === 12) {
                this.setGradient(result_charasters[selected_charaster]["color"])
            }
        }
    }

    componentDidMount() {
        bridge.send("VKWebAppInit");
    }

    hexToRgbA(hex, transparency = 1) {
        let c;

        if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
            c = hex.substring(1).split('');
            if (c.length === 3) {
                c = [c[0], c[0], c[1], c[1], c[2], c[2]];
            }
            c = '0x' + c.join('');
            return 'rgba(' + [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',') + ',' + transparency + ')';
        }
    }

    setGradient(color) {
        let color_hex_1 = this.hexToRgbA(color, 0.6);
        let color_hex_2 = this.hexToRgbA(color);

        let style = 'linear-gradient(to bottom, rgba(9, 9, 121, 0) 0%, ' + color_hex_1 + ' 38%, ' + color_hex_2 + ' 90%)';
        style = '<style>.question_box:after {background-image: ' + style + ' !important; };</style>';

        let style_div = document.getElementById("gradient_style");
        style_div.innerHTML = style
    }

    changeAge(e) {
        let age = parseInt(e.target.value);

        if (age > 0) {
            this.setState({
                next_button_active: 1
            })
        } else {
            this.setState({
                next_button_active: 0
            })
        }
    }

    async share_result(reset = 0) {
        await bridge.send("VKWebAppShowStoryBox", {
            "background_type": "image",
            /* "url": `${window.location.origin}/AnimeTest/results/${this.state.selected_charaster}.jpg`, */
            "url": `${window.location.origin}/AnimeTest/results/1.jpg`,
            "locked": true,
            "attachment": {
                "text": "go_to",
                "type": "url",
                "url": link_vk_app
            }
        }).then(() => {
            if (reset) {
                this.setState({
                    gender: 0,
                    answer: 0,
                    step: 0,
                    next_button_active: 0,
                    share_in_story: 1
                });
            } else {
                this.setState({
                    share_in_story: 1
                });
            }
        }).catch(() => {
            if (reset) {
                this.resetState();
            }
        });
    }

