import React, {useState, useEffect} from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import '@vkontakte/vkui/dist/vkui.css';
import './css/main.css';

import Home from './panels/Home';

import {ModalRoot, ModalCard} from "@vkontakte/vkui";

import Icon28StoryOutline from '@vkontakte/icons/dist/28/story_outline';

const App = () => {
    const [activeModal, setActiveModal] = useState(null);
    const [shareInStory, setShareInStory] = useState(null);

    useEffect(() => {
        bridge.subscribe(({detail: {type, data}}) => {
            if (type === 'VKWebAppUpdateConfig') {
                const schemeAttribute = document.createAttribute('scheme');
                schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
                document.body.attributes.setNamedItem(schemeAttribute);
            }
        });
    }, []);

    const modalBack = () => {
        setActiveModal(null)
    };

    const storyShare = (a) => {
        setActiveModal(null);

        setShareInStory(a);
    };

    const ShowStoryModal = () => {
        setActiveModal("STORY_CARD");
    };

    const modal = (
        <ModalRoot
            activeModal={activeModal}
            onClose={modalBack}
        >
            <ModalCard
                id="STORY_CARD"
                onClose={() => setActiveModal(null)}
                icon={<Icon28StoryOutline width={56} height={56}/>}
                header="Поделиться результатом в истории?"
                actions={[{
                    title: 'Нет',
                    mode: 'secondary',
                    action: () => storyShare(0)
                }, {
                    title: 'Да',
                    mode: 'primary',
                    action: () => storyShare(1)
                }]}
            />
        </ModalRoot>
    );

    return (
        <View activePanel="home" modal={modal}>
            <Home
                id="home"
                ShowStoryModal={ShowStoryModal}
                shareInStory={shareInStory}
                setShareInStory={setShareInStory}
            />
        </View>
    );
}

export default App;